import React, {useState, useMemo, useEffect, useCallback} from 'react';
import {View, Text, TouchableOpacity} from 'react-native';
import {Calendar, Icon, StyleType} from '@ui-kitten/components';
import HeaderCM from '../../../components/Header/HeaderCM';
import TextCM from '../../../components/Text';
import {ICheckInOutRecord} from '../../interfaces';
import {DateUtils} from '../../../../utils';
import {Font} from '../../../../constants';
import {IconEmpty} from '../../../../assets/images';
import {CalendarDateInfo} from '@ui-kitten/components/ui/calendar/type';
import {styles} from './styles';
import {listStatusCheckin} from './helper';
import {useTranslation} from 'react-i18next';
import {useFocusEffect} from '@react-navigation/native';

type Props = {
  onClose?: () => void;
  dataCheckInOut: ICheckInOutRecord[];
  getDataCheckin: (
    d?: Date,
    workLocation?: 'default' | 'onsite' | 'all',
  ) => void;
};

type DayStatus = 0 | 1 | 2;

const CalendarDetail: React.FC<Props> = ({
  onClose,
  dataCheckInOut,
  getDataCheckin,
}) => {
  const {t} = useTranslation();
  const [selectedDate, setSelectedDate] = useState<Date>(new Date());
  const [detailCheckin, setDetailCheckinout] = useState<any>();

  const dayStatusMap = useMemo<Record<string, DayStatus>>(() => {
    const map: Record<string, DayStatus> = {};
    dataCheckInOut?.forEach(({checkinTime, checkoutTime}) => {
      const date = new Date(checkinTime);
      const key = `${date.getFullYear()}${date.getMonth()}${date.getDate()}`;
      map[key] = checkoutTime ? 2 : (Math.max(map[key] || 0, 1) as DayStatus);
    });
    return map;
  }, [dataCheckInOut]);

  const renderDay = (
    info: CalendarDateInfo<Date>,
    style: StyleType,
  ): React.ReactElement => {
    const actualDate = new Date(info.date);
    let key = `${actualDate.getFullYear()}${actualDate.getMonth()}${actualDate.getDate()}`;
    const status: DayStatus = dayStatusMap[key] ?? 0;

    let container = styles.defaultContainer;
    let textStyle = styles.defaultText;
    if (status === 1) {
      container = styles.checkinOnlyContainer;
      textStyle = styles.checkinOnlyText;
    } else if (status === 2) {
      container = styles.bothContainer;
      textStyle = styles.bothText;
    }
    const isSelected =
      actualDate.toDateString() === selectedDate.toDateString();

    return (
      <View
        style={[
          style.style,
          container,
          isSelected && styles.selectedContainer,
        ]}>
        <Text
          style={[
            textStyle,
            isSelected && styles.selectedText,
            info.bounding && {color: '#B0B0B0'},
          ]}>
          {actualDate.getDate()}
        </Text>
      </View>
    );
  };

  useFocusEffect(
    useCallback(() => {
      getDataCheckin();
    }, []),
  );

  const findInfoDay = (day: Date | string = new Date().toUTCString()) => {
    const dateChanged = new Date(day);
    let findData = dataCheckInOut.find(item => {
      const dateCheck = new Date(item.checkinTime);
      return (
        dateCheck.getFullYear() === dateChanged.getFullYear() &&
        dateCheck.getMonth() === dateChanged.getMonth() &&
        dateCheck.getDate() === dateChanged.getDate()
      );
    });
    console.log('findData', findData);
    // nếu data có được là onsite thì tìm ảnh theo api nào đấy. Chưa có api, cũng chưa checkin được nên chưa có ảnh
    setDetailCheckinout(findData);
  };

  useEffect(() => {
    findInfoDay();
  }, []);

  return (
    <>
      <HeaderCM
        style={{backgroundColor: '#fff'}}
        contentStyle={{backgroundColor: '#fff'}}
        titleStyle={{color: '#000810'}}
        fillIconBackLeft="#000810"
        title="Lịch sử điểm danh"
        onPressIconLeft={onClose}
      />
      {/* <View style={{padding: 16}}>
        {listStatusCheckin.map((item: any) => {
          const style = item.style;
          return (
            <View
              style={{
                flexDirection: 'row', // sắp xếp ô vuông và label theo hàng :contentReference[oaicite:2]{index=2}
                alignItems: 'center',
                marginVertical: 4,
              }}>
              <View
                style={[
                  {
                    width: 16,
                    height: 16,
                    borderWidth: 1,
                    borderRadius: 2,
                    marginRight: 8,
                  },
                  {
                    backgroundColor: style.backgroundColor,
                    borderColor: style.borderColor,
                  },
                ]}
              />
              <Text style={[{fontSize: 16}]}>{item.name}</Text>
            </View>
          );
        })}
      </View> */}

      <Calendar
        style={{width: '100%'}}
        date={selectedDate}
        min={new Date(1900, 0, 1)}
        renderDay={renderDay}
        onSelect={nextDate => {
          setSelectedDate(nextDate);
          findInfoDay(nextDate);
        }}
        max={new Date(2300, 0, 1)}
        onVisibleDateChange={(D, CalendarViewMode) => {
          getDataCheckin(D);
        }}
      />

      <View style={{padding: 16, flex: 1}}>
        <TextCM style={{fontFamily: Font.InterSemiBold600, fontSize: 16}}>
          {DateUtils.formatDateVietnamese(selectedDate)}
        </TextCM>
        {detailCheckin ? (
          <>
            {detailCheckin?.checkinTime && (
              <TextCM
                style={{
                  fontSize: 14,
                  marginTop: 8,
                }}>
                {t('DB.etms.timeCheckSuccess', {
                  type: 'in',
                  time: DateUtils.getCurrentTime(detailCheckin?.checkinTime),
                })}
              </TextCM>
            )}
            {detailCheckin?.checkoutTime && (
              <TextCM
                style={{
                  fontSize: 14,
                  marginTop: 8,
                }}>
                {t('DB.etms.timeCheckSuccess', {
                  type: 'out',
                  time: DateUtils.getCurrentTime(detailCheckin?.checkoutTime),
                })}
              </TextCM>
            )}
            <Text style={{margin: 10}}>
              {detailCheckin.late && detailCheckin.overTime
                ? 'Check-in muộn, check-out sớm'
                : detailCheckin.late
                ? 'Check-in muộn'
                : detailCheckin.overTime
                ? 'Check-out sớm'
                : ''}
            </Text>
          </>
        ) : (
          <View
            style={{
              alignItems: 'center',
              justifyContent: 'center',
              flex: 1,
              rowGap: 8,
            }}>
            <IconEmpty width={218} height={149} />
            <TextCM>{t('DB.etms.noInfomation')}</TextCM>
          </View>
        )}
      </View>
    </>
  );
};

export default CalendarDetail;
